'use strict';

// MODULES //

import normalizeIndex from '@stdlib/ndarray-base-normalize-index';
import ndims from '@stdlib/ndarray-base-ndims';
import getStrides from '@stdlib/ndarray-base-strides';
import format from '@stdlib/error-tools-fmtprodmsg';


// MAIN //

/**
* Returns the stride along a specified dimension for a provided ndarray.
*
* @param {ndarrayLike} x - input ndarray
* @param {integer} dim - dimension index
* @throws {TypeError} first argument must be an ndarray having one or more dimensions
* @throws {RangeError} dimension index exceeds the number of dimensions
* @returns {integer} stride
*
* @example
* import zeros from '@stdlib/ndarray-zeros';
*
* var out = stride( zeros( [ 3, 3, 3 ] ), 0 );
* // returns 9
*/
function stride( x, dim ) {
	var N;
	var d;

	// Retrieve array meta data:
	N = ndims( x );

	// Check whether we were provided a zero-dimensional array...
	if ( N === 0 ) {
		throw new TypeError( format( '1mhF7', N ) );
	}
	// Normalize the dimension index:
	d = normalizeIndex( dim, N-1 );
	if ( d === -1 ) {
		throw new RangeError( format( '1mhF8', N, dim ) );
	}
	// Return the array stride:
	return getStrides( x, false )[ d ];
}


// EXPORTS //

export default stride;
